import {
  FormControl,
  InputLabel,
  makeStyles,
  MenuItem,
  Select,
} from "@material-ui/core"
import { graphql, navigate, useStaticQuery } from "gatsby"
import kebabCase from "lodash/kebabCase"
import React from "react"
import { CategorySelectQuery } from "../../../graphql-types"

const handleChange = event => {
  if (event.target.value === "All") {
    navigate(`/`)
  } else {
    navigate(`/categories/${kebabCase(event.target.value)}`)
  }
}

const CategorySelect = ({ selectedCategory }) => {
  const classes = useStyles()
  console.log(selectedCategory)
  const data = useStaticQuery(graphql`
    query CategorySelect {
      totalCount: allMarkdownRemark(limit: 2000) {
        totalCount
      }
      groupedCategory: allMarkdownRemark(limit: 2000) {
        group(field: frontmatter___categories) {
          fieldValue
          totalCount
        }
      }
    }
  `) as CategorySelectQuery

  return (
    <FormControl variant="outlined" className={classes.root}>
      <InputLabel id="demo-simple-select-outlined-label">Category</InputLabel>
      <Select
        labelId="demo-simple-select-outlined-label"
        id="demo-simple-select-outlined"
        value={selectedCategory}
        onChange={handleChange}
        label="category"
      >
        <MenuItem value="All" key={0}>
          All ({data.totalCount.totalCount})
        </MenuItem>
        {data.groupedCategory.group.map((category, index) => (
          <MenuItem value={category.fieldValue} key={index}>
            {category.fieldValue} ({category.totalCount})
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  )
}

export default CategorySelect

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(0, 0, 1, 0),
    width: "100%",
    minWidth: 120,
  },
}))
